  text-align: center;
  font-weight: bold;
  color: #14d288;
  margin-top: 10px;
  font-size: 130%
}
.msg_table {
  width: 100%;
  height: 100%;
  /deep/.el-table__body-wrapper {
    cursor: pointer;
    overflow: auto;
  }
  /deep/ .el-table__body-wrapper::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 5px; /*高宽分别对应横竖滚动条的尺寸*/
    height: 4px;
    border-radius: 10px;
  }
  /deep/ .el-table__body-wrapper::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
